.friendlist{
    width: 300px;
    margin-left: auto;
    margin-right: auto;
}
.item{
    box-shadow: 0px 0px 8px 0px #626262;
      display: flex;
    flex-direction: row;
   align-items: center;
   gap: 20px;
   margin-bottom: 20px;
  padding: 10px;
  border-radius: 4px;
}

.statusOn {
    width: 18px;
    height: 18px;

    border-radius: 50%;
    background-color: #48bf0c;
}
.statusOff {
    width: 18px;
    height: 18px;

    border-radius: 50%;
    background-color: #f20909;
}
.avatar{
    border-radius: 4px;
    background-color: #626262;
}
.name{
    font-weight: 700;
    font-size: 24px;
}