.transactionhistory{
    width: 450px;
    margin-left: auto;
    margin-right: auto;
box-shadow: 0px 0px 8px 0px #626262;

}

tbody tr:nth-child(odd) {
    background-color: #fff;
    color: #626262;
    align-content: center;
}

tbody tr:nth-child(even) {
    background-color: #69a4ba;
    color: #626262;
}
thead {
    background-color: #43ecfb;
}

table {
    background-color: #fff;
    color:#fff;
    align-items: center;
}
tbody td {
    text-align: center;
}