@import-normalize; /* bring in normalize.css styles */

body {
  font-family: sans-serif;
  line-height: 1.5;

}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  /* Убираем маркеры */
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}